import React from 'react';
import {DialogComment} from './dialog.js';
const Comments = () => {
    var state = React.useState({
        commentDialog: false,
    });
    var [getState, setState] = state;
    const commentList = () => {

        const comments = [
            {
                user: "Sekii",
                message: "Heey!",
                date: "29.11.2022"
            },
            {
                user: "Uwe23",
                message: "Mh...",
                date: "12.06.2022"
            }
        ];

        return comments.map((element, index) => {
            return (
                <div key={index}
                    class="comment">
                    <div class="comment-user">
                        {
                        element.user
                    }</div>
                    <p class="comment-date">
                        {
                        element.date
                    }</p>
                    <p class="comment-message">
                        {
                        element.message
                    }</p>
                </div>
            );
        });
    }
    
    return (
        <>
        <DialogComment isActive={getState.commentDialog} setState={setState}/>
        <section id="comment-section">
            <h1>Kommentare</h1>
            {
            commentList()
        }
            <button id="write-comment-btn" class="btn" onClick={() => {
            setState({
                commentDialog: true
            })
            }}>Kommentar schreiben</button>
        </section>
        </>
    )
}
export default Comments;
