import React from 'react';
import '../styles/glitch.css';
import Comments from '../components/comments.js';
import {DialogContact} from '../components/dialog.js';

const Home = () => {
    const glitch = "EricSource";

    var state = React.useState({
        contactDialog: false,
    });
    var [getState, setState] = state;

    return (
        <div id="home">
            <DialogContact isActive={getState.contactDialog} setState={setState}/>
            <section id="main-content" tabIndex="-1">
                <div class="container">
                    <div class="row align-items-center"
                        style={
                            {padding: 16}
                    }>
                        <article>
                            <div class="glitch-wrapper">
                                <h1 class="glitch intro_text"
                                    data-text={glitch}>
                                    {glitch}</h1>
                            </div>
                            <p>Hier könnte <strong>wirklich</strong> was tolles stehen...</p>
                            <footer>
                                <span></span>
                                <button class="btn-primary btn" onClick={
                                    () => {
                                    setState({contactDialog: true})
                                    }
                                }>Kontaktieren</button>
                            </footer>
                        </article>
                        <Comments/>

                    </div>
                </div>
            </section>
        </div>
    );
}


export default Home;
